.landing-list {
  background: url("../img/whitenoise-bg.webp") no-repeat center/100% 100%, linear-gradient(136.82deg, rgba(137, 110, 78, 0) 24.2%, #715a40 43.55%, #896e4e 69.26%), var(--secondary-accent-color);
  color: var(--light-color);
}
@media (max-width: 480px) {
  .landing-list__content {
    max-width: 408px;
  }
}
.landing-list__content-title {
  font-family: "RubikMonoOne";
  font-style: normal;
  font-weight: 400;
  font-size: 38px;
  line-height: 47px;
}
@media (max-width: 480px) {
  .landing-list__content-title {
    font-size: 26px;
    line-height: 32px;
  }
}
.landing-list__content-subtitle {
  margin-top: 40px;
  font-size: 24px;
  line-height: 29px;
}
.landing-list__content-subtitle span {
  font-weight: 500;
}
.landing-list__content-subtitle:last-child {
  margin-top: 10px;
}
.landing-list__content-subtitle:last-child a {
  color: inherit;
  text-decoration: underline;
}
@media (max-width: 480px) {
  .landing-list__content-subtitle {
    font-size: 20px;
    line-height: 25px;
  }
}
.landing-list__content-list {
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}
.landing-list__content-list li {
  margin-top: 40px;
}
@media (max-width: 480px) {
  .landing-list__content-list {
    font-size: 20px;
    line-height: 25px;
  }
}